/* Файл blocks/main-header.less */
.main-header {
  margin: 0;
  padding: 0;
  display: flex;
}
.main-header .header-logo {
  padding-top: 22px;
  padding-left: 20px;
  padding-bottom: 25px;
}
.main-navigation {
  display: flex;
  flex-direction: column;
}
.main-navigation .main-navigation__wrapper ul {
  text-align: center;
  margin: 0;
  padding: 0;
}
.main-navigation__list li {
  padding-top: 7px;
  padding-bottom: 7px;
}
.main-navigation__list a {
  text-decoration: none;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
}
/* Файл blocks/page-main.less */
.intro-mobile .button {
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  text-transform: none;
}
.intro-mobile li {
  list-style: none;
}
.intro-mobile p {
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  text-transform: none;
}
/* Файл blocks/information.less */
.information {
  background-color: #f2f2f2;
}
.information .info {
  background-color: #ffffff;
  color: #000000;
  padding-bottom: 30px;
}
.information .info .info-text {
  font-size: 24px;
  font-weight: 300;
  text-transform: none;
  color: #283645;
}
.information .info-advantages {
  background-color: #f2f2f2;
  text-transform: none;
}
.information .info-advantages .info-advantages__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  color: #000000;
}
.information .info-advantages .info-advantages__text {
  color: #283645;
  font-size: 14px;
  font-weight: 400;
}
/* Файл blocks/reivew.less */
.reivew {
  background-color: #f2f2f2;
  text-transform: none;
  text-align: center;
}
.reivew .reivew__list .reivew-name {
  padding-top: 49px;
  padding-bottom: 21px;
  color: #000000;
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
}
.reivew .reivew__list .reivew-text {
  color: #283645;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  padding-bottom: 42px;
}
/* Файл blocks/price.less */
.price {
  background-color: #f2f2f2;
  text-transform: uppercase;
}
.price .price__title {
  background-color: #283645;
  color: #ffffff;
  align-items: center;
}
.price .price__title .price__rate {
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
}
.price .price__title .price__col {
  font-size: 24px;
  font-weight: 300;
  line-height: 30px;
}
.price .price__list {
  background-color: #ffffff;
  border-top: 2px solid #e5e5e5;
  border-bottom: 2px solid #e5e5e5;
}
.price .price__list .price__text {
  color: #283645;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  text-transform: uppercase;
}
/* Файл blocks/main-footer.less */
.main-footer {
  background-color: #ffffff;
}
.main-footer .main-footer__contacts {
  text-transform: none;
  text-align: center;
}
.main-footer .main-footer__contacts .contacts__title {
  color: #283645;
  font-size: 24px;
  font-weight: 300;
}
.main-footer .main-footer__contacts .contacts__text {
  color: #283645;
  font-size: 18px;
  font-weight: 700;
}
.main-footer .main-footer__contacts .contacts__item {
  color: #d22856;
  font-size: 24px;
  font-weight: 300;
  font-style: normal;
}
/* Файл blocks/page-photo.less */
.page-photo .page-title {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 320px;
}
.page-photo .page-title h2 {
  text-transform: none;
  text-align: center;
  color: #ffffff;
  font-size: 24px;
  font-weight: 300;
}
/* Файл blocks/page-gallery.less */
.page-gallery {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
}
.page-gallery .page__text {
  padding-left: 35px;
  padding-right: 35px;
  padding-bottom: 40px;
  text-align: center;
  text-transform: none;
  color: #283645;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
}
.page-gallery .page-gallery__photo .author-photo {
  align-items: center;
}
.page-gallery .page-gallery__photo .author-photo .author__text {
  text-transform: none;
  background-color: #f2f2f2;
  margin-left: 20px;
  margin-right: 20px;
  padding-top: 25px;
  padding-left: 17px;
  padding-right: 23px;
}
.page-gallery .page-gallery__photo .author-photo .author__text .author__name {
  color: #283645;
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
}
.page-gallery .page-gallery__photo .author-photo .author__text .author__publication-time {
  font-size: 12px;
  font-weight: 300;
  color: #a8abaf;
  text-transform: uppercase;
}
.page-gallery .page-gallery__photo .author-photo .author__text .author__publication-text {
  color: #283645;
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
}
.page-gallery .page-gallery__photo .author-photo .author__text .author__hearts-col {
  color: #d22856;
  font-size: 12px;
  font-weight: 400;
  line-height: 24px;
  text-transform: uppercase;
}
/* Файл blocks/upload.less */
.upload {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #f2f2f2;
}
.upload .upload-btn .upload-btn__toggle,
.upload .upload-btn .cancel-btn__toggle {
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
}
/* Файл blocks/photo-footer.less */
.photo-footer {
  padding: auto;
  display: flex;
  justify-content: space-between;
  background-color: #ffffff;
  flex-direction: column;
  align-items: center;
  padding-top: 40px;
  bottom: 0;
  left: 0;
  right: 0;
}
.photo-footer ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.photo-footer ul .social__list li {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.photo-footer .photo-footer__copyright .copyright__text {
  color: #283645;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}
.photo-footer .photo-footer__copyright .copyright__link {
  width: 27px;
  height: 34px;
  background-image: url("source/img/logo-pink-white-mobile.svg");
  background-repeat: no-repeat;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  background-color: #1d2631;
  color: #ffffff;
  text-transform: uppercase;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.general {
  width: 320px;
  margin: auto;
  padding: auto;
}
